import { Component, OnInit } from '@angular/core'
import { catchError, debounceTime, distinctUntilChanged, takeUntil, throwError } from 'rxjs'
import { ApiService } from '@core/services/api.service'
import { ProductDelivery } from '@core/models/product.model'
import { FormBuilder, FormControl } from '@angular/forms'
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog'
import { DialogLoginComponent } from '../../auth/components/dialog-login/dialog-login.component'
import { AuthUserService } from '@core/services/auth-user.service'
import { User } from '@core/models/user.model'
import { Router } from '@angular/router'
import { Cart, CartProduct } from '@core/models/cart.model'
import { CartService } from '@core/services/cart.service'
import { Form } from '@core/common/form2'
import { HttpErrorResponse } from '@angular/common/http'

@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss']
})
export class IndexComponent extends Form implements OnInit {

  public steps = ['Cart', 'Order details', 'Order summary', 'Place order']
  public active = 0
  // public products?: Array<CartProduct>
  public total = 0
  public delivery?: ProductDelivery
  public voucherField: FormControl = new FormControl()
  public qtyField: FormControl = new FormControl(1)
  private user?: User|null
  public cart: Cart

  constructor(
    private api: ApiService,
    private authService: AuthUserService,
    private cartService: CartService,
    private router: Router,
    private dialog: MatDialog,
    private fb: FormBuilder
  ) {
    super()
    this._initFormControl(fb, {})
  }

  ngOnInit(): void {

    this.authService.getAuthUser().subscribe((user) => this.user = user)

    this.cartService.getCart().subscribe(cart => {
      this.cart = cart

      this.cart?.products.forEach((product) => {
        this.setFCBYName(`${product.id}`, null, product.quantity)
      })
    })
  }

  showSigninDialog() {
    const dialogRef = this.dialog.open(DialogLoginComponent, {
      width: '428px',
      maxWidth: '99vw'
    })

    dialogRef.beforeClosed()
      .pipe(takeUntil(this.destroy$))
      .subscribe(() => {
        if (this.user) {
          this.gotoCheckout()
        }
      })

  }

  public gotoCheckout() {
    if (this.user) {
      this.router.navigate(['/checkout'])
    } else {
      this.showSigninDialog()
    }
  }

  private calculateCartTotal() {
    this.cart.total = this.cart.products.reduce((carry, product) => {
      return carry + product.quantity * product.price.priceWithVat
    }, 0)
  }

  public updateQty(product: CartProduct, qty: number) {
    const originalQty = product.quantity
    product.quantity = qty
    this.setFCBYName(`${product.id}`, null, qty)
    this.calculateCartTotal()

    this.cartService.updateQty(product, qty)
      .pipe(debounceTime(150), distinctUntilChanged())
      .pipe(catchError((error: HttpErrorResponse) => {
        this.setFCBYName(`${product.id}`, null, originalQty)
        product.quantity = originalQty
        this.calculateCartTotal()
        return throwError(() => error)
      }))
      .subscribe(() => {
        // product.quantity = qty
      })
  }

  incQty(product: CartProduct) {
    const qty = (this.getFC(`${product.id}`))?.getRawValue() ?? 0
    this.updateQty(product, qty + 1)
  }

  decQty(product: CartProduct) {
    let qty = (this.getFC(`${product.id}`))?.getRawValue() || 1
    qty -= 1
    if (qty > 0) {
      this.updateQty(product, qty)
    }
  }

  public removeProduct(product: CartProduct) {
    this.cartService.removeProduct(product).subscribe()
  }
}
