import {Component, EventEmitter, Input, Output} from '@angular/core'
import { Condition } from '@core/models/product.model'
import {CartProduct} from "@core/models/cart.model";
import {MatLegacySelectChange as MatSelectChange} from "@angular/material/legacy-select";

@Component({
  selector: 'app-cart-product',
  templateUrl: './product.component.html',
  styleUrls: ['./product.component.scss']
})
export class ProductComponent {

  @Input()
  public product: CartProduct

  @Output()
  private qtyUpdated = new EventEmitter<number>()

  @Output()
  private productRemoved = new EventEmitter<CartProduct>()

  availableQtys = Array.from({length: 30}, (_, i) => i + 1)
  readonly Condition = Condition

  updateQty(qty: MatSelectChange) {
    this.qtyUpdated.emit(qty.value)
  }

  public removeCartProduct() {
    this.productRemoved.emit(this.product)
  }

}
