import { Component, EventEmitter, OnInit, Output } from '@angular/core'
import { Product } from '@core/models/product.model'
import { ApiService } from '@core/services/api.service'
import { takeUntil } from 'rxjs'
import { AutoDestroy } from '@core/common/auto-destroy'
import {MicroserviceType} from "@core/enums/microservice-type.enum";
import {Cart, CartProduct} from "@core/models/cart.model";
import {CartService} from "@core/services/cart.service";

@Component({
  selector: 'app-cart-popup',
  templateUrl: './cart-popup.component.html',
  styleUrls: ['./cart-popup.component.scss']
})
export class CartPopupComponent extends AutoDestroy implements OnInit {
  @Output()
  public cancelled: EventEmitter<any> = new EventEmitter<any>()

  public cart?: Cart

  constructor(private api: ApiService, private cartService: CartService) {
    super()
  }

  ngOnInit(): void {
    this.cartService.getCart().subscribe(cart => {
      this.cart = cart;
    })

    this.cartService.fetchCart();
  }

  cancel() {
    this.cancelled.emit()
  }
}
